body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow-x:hidden;
  overflow-y:hidden;
}
.week{
  width:100%;

  text-align: center;
  background: -webkit-gradient(linear, left top, right top, from(#ffc72d), to(rgba(72, 159, 230, 0.69)));
  background: -webkit-linear-gradient(left, #ffc72d, rgba(72, 159, 230, 0.69));
  background: -o-linear-gradient(left, #ffc72d, rgba(72, 159, 230, 0.69));
  background: linear-gradient(to right, #ffc72d, rgba(72, 159, 230, 0.69));
  height:100vh;
  padding-top: 10%;
}
.week h2,h3 {
  color: #8e8e8e;
}
  .wellcome{
    background: -webkit-gradient(linear, left top, right top, from(#8e8e8e), to(rgba(85, 83, 142, 0.69)));
    background: -webkit-linear-gradient(left, #8e8e8e, rgba(85, 83, 142, 0.69));
    background: -o-linear-gradient(left, #8e8e8e, rgba(85, 83, 142, 0.69));
    background: linear-gradient(to right, #8e8e8e, rgba(85, 83, 142, 0.69));
    height:100vh;
    width:100%;
    padding: 2%;
  }
    .wellcome h1,h2{
  text-align: center;
      padding-right: 4%;
      padding-top: 2%;

      color:white;
    }
    .form{
        text-align: center;
      padding-right: 4%;
    }


    .header{
        width: 100%;
         height:5%;
        background-color: #8e8e8e;
    }
    .footer{
      width: 100%;
      height:2%;
      background-color: #8e8e8e;
    }


